body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
  font-family: 'Roboto', "Arial", sans-serif;
  color: #030c17;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  background: linear-gradient(90deg, #fbf9ec 0%, #fbf9ec 80%, #eceaf7 90%, #fbf9ec 100%);
}
.header {
  position: fixed;
  top: 0;
  z-index: 40;
  display: flex;
  width: 100%;
  min-height: 50px;
  padding-bottom: 2px;
  border-bottom: 1px solid #030c17;
  background-color: #ffffff;
}
.header__icon {
  width: 50px;
  min-height: 50px;
  margin-left: 15px;
}
.timeline {
  display: flex;
  align-self: center;
  flex-grow: 1;
}
.timeline__toggle {
  position: relative;
  margin-left: 30px;
  padding-right: 40px;
  font-size: 28px;
  border: none;
  background-color: inherit;
  cursor: pointer;
}
.timeline__toggle:hover,
.timeline__toggle:focus {
  color: #009eb5;
  outline: none;
}
.timeline__toggle:active {
  color: #009eb5;
  opacity: 0.4;
  outline: none;
}
.timeline__list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  flex-grow: 1;
  list-style: none;
  padding: 0;
}
.timeline__date {
  margin-top: 4px;
  margin-bottom: 4px;
  margin-right: 25px;
}
.timeline__link {
  padding: 0 6px;
  font-size: 22px;
  color: #030c17;
  text-decoration: none;
}
.timeline__link:hover {
  color: #009eb5;
  opacity: 0.67;
}
.timeline__link:active {
  color: #009eb5;
  opacity: 0.2;
}
.gallery {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  padding-top: 40px;
  background-image: url(../img/back1.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}
.gallery__item {
  position: relative;
  display: flex;
  justify-content: center;
  width: 80vh;
  height: 100vh;
  margin-bottom: 10px;
}
.gallery__item--slider {
  position: relative;
  width: 50%;
}
.gallery__item::after,
.gallery__item--slider + .gallery__item::before {
  content: url(../img/separator.svg);
  position: absolute;
  display: block;
  bottom: 15px;
  right: 0;
  width: 80vh;
  height: 10px;
}
.gallery__item--slider + .gallery__item::before {
  top: 0;
}
.photo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;
}
.photo__wrapper {
  position: relative;
  margin: 0;
  padding: 0;
  background-color: #030c17;
  overflow: hidden;
}
.photo__main {
  margin: 0;
}
.photo__img {
  position: relative;
  width: 80vh;
  height: 80vh;
  margin: 0;
  padding: 0;
  vertical-align: bottom;
  border: 1px solid #fbf9ec;
  border-bottom: none;
  transition: opacity 0.95s;
  object-fit: cover;
  cursor: pointer;
}
.photo__img:hover {
  opacity: 0.4;
}
.photo__description {
  position: absolute;
  top: 50%;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: #eceaf7;
  font-size: 22px;
  text-align: center;
  font-style: italic;
  opacity: 0;
  transition: opacity 1.65s;
  pointer-events: none;
}
.photo__wrapper:hover .photo__description {
  opacity: 1;
}
.photo__date {
  width: 80vh;
  margin: 0;
  padding: 0;
  padding: 10px 0;
  border: 1px solid #030c17;
  text-align: center;
  background-color: #ffffff;
}
.photo--mini .photo__img {
  width: 150px;
}
.photo--mini .photo__date,
.photo--mini .photo__description {
  display: none;
}
@media (max-width: 780px) {
  .photo--mini {
    display: none;
  }
}
.sublist {
  position: relative;
  display: flex;
  flex-direction: row;
  padding: 0;
  list-style: none;
}
.sublist__button {
  position: absolute;
  top: 45vh;
  width: 0;
  height: 0;
  border-style: solid;
  background-color: transparent;
  cursor: pointer;
  pointer-events: none;
}
.sublist__button--left {
  left: 10px;
  border-width: 50px 25px 50px 0;
  border-color: transparent red transparent transparent;
}
.sublist__button--right {
  right: 10px;
  border-width: 50px 0 50px 25px;
  border-color: transparent transparent transparent red;
}
